* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

body {
  background-image: url(https://8b.com/assets/images/securebg.png);
}

.mt-10 {
  margin-top: 60px;
}

h1,h2,h3,h4,h5,h6,p,label{
  /* color: #45460c; */
  color: #183153
}

.nav-item.active a{
  color: #ff7f00 !important;
  border-bottom: 3px solid #8f931f;
}

a.link{
  color: #ff7f00 !important;
}

.carousel-height{
  height: calc(90vh);
}
.carousel-caption{
    position: absolute;
    right: 15%;
    top: 20%;
    left: 15%;
    z-index: 10;
    color: #fff;
    width: 40%;
}

.parallax {
  min-height: calc(60vh);
  background-attachment: fixed;
  background-position: center left;
  background-repeat: no-repeat;
  background-size: cover;
}

.parallax_physio {
  background-image: linear-gradient(rgba(0,0,0, 0.6), rgba(0,0,0, 0.6)), url("https://images.squarespace-cdn.com/content/v1/59889a412994ca0cad663d9b/1609262681385-U4L50UDPDD6UAUV76VY1/ke17ZwdGBToddI8pDm48kHL51p843nMyQnzXEKPZUlp7gQa3H78H3Y0txjaiv_0fDoOvxcdMmMKkDsyUqMSsMWxHk725yiiHCCLfrh8O1z5QPOohDIaIeljMHgDF5CVlOqpeNLcJ80NK65_fV7S1UcxU5SrblpurgaCJMhsEzuxwenxBP6c9yOt2hKUcJof_3WUfc_ZsVm9Mi1E6FasEnQ/Front-page-banner-1.png?format=1000w");
}

.parallax_contact {
  background-image: linear-gradient(rgba(0,0,0, 0.6), rgba(0,0,0, 0.6)), url("https://www.smg-marble.com/wp-content/uploads/2016/10/contactus-banner.jpg");
  min-height: calc(50vh) !important;
}

button:focus {
  outline: none !important;
}

.customBtn .MuiButton-label{
  color: #fff
}

/* material verwrite css  */
.MuiStepIcon-root.MuiStepIcon-completed{
  color: #ff7f00 !important;
}

.rotateBtn {
  transform: rotate(180deg);
}

.pointer {
  cursor: pointer;
}

.w-10{
  width: 10% !important
}

/* admin page  */
.admin_container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.admin_menu{
  display: flex;
  flex-direction: column;
  width: 10%;
}
.admin_content{
  width: 75%;
  margin: 10px auto;
}
.admin_image{
  width: 50%
}
/* admin page end  */

@media screen and (min-width: 320px) and (max-width: 960px) {
  .carousel-height {
    height: calc(60vh);
  }
  .carousel-caption{
    position: absolute;
    width: 75%;
  }
  .admin_container{
    flex-direction: column;
  }
  .admin_menu{
    flex-direction: row !important;
    width: 100% !important;
    margin-bottom: 10px;
  }
  .admin_content{
    width: 100%;
    margin: 0 auto;
  }
  .admin_image{
    width: 75%
  }
}